import React from 'react'

export const Footer = () => {
    let footerstyle = {
        position: "relative",
        top: "70vh",
        width: "100%",
        border: "2.5px groove darkblue"

    }
    return (
        <footer className="bg-dark text-light py-4" style={footerstyle}>
            <p className="text-center">
                Developed By: <a href="https://github.com/uvyuva" target="_blank">Yuvaraj<i class="fab fa-github"></i></a>
            </p>
        </footer>
    )
}
